@use 'sass:color';
@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario{
    margin-top: 5rem;

    .campo{
        display: flex;
        align-items: center;
        margin-bottom: 2rem;
        
        label{
            flex: 0 0 10rem;
        }
    
        input{
            flex: 1;
            border: none;
            padding: 1rem;
            background-color: color.adjust(v.$gris, $lightness: 25%);
        }
    }


}



